<template>
  <div id="login">
    <el-form
      :model="loginForm"
      :rules="loginRules"
      ref="loginForm"
      label-width="0px">
      <h3>系统登录</h3>
      <el-form-item prop="account">
        <el-input
            type="text"
            placeholder="请输入账号"
            v-model="loginForm.account"
            prefix-icon="el-icon-user"
            clearable
        >
        </el-input>
      </el-form-item>
      <el-form-item prop="password">
        <el-input
            placeholder="请输入密码"
            v-model="loginForm.password"
            prefix-icon="el-icon-lock"
            show-password
            @keyup.enter.native="handleLogin"
        >
        </el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" style="width: 100%" :loading="loading" @click="handleLogin">登录</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  data() {
    return {
      loading: false,
      loginForm: {
        account: '',
        password: ''
      },
      loginRules: {
        account: [
          { required: true, message: '请输入账号', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' }
        ]
      }
    };
  },

  methods: {
    handleLogin() {
      this.$refs['loginForm'].validate((valid) => {
        this.loading = true;
        if (valid) {
          this.$store.dispatch('LOGIN', this.loginForm).then((res) => {
            this.loading = false;
            this.$router.push('/home');
          }).catch((e) => {
            this.$message.error(e);
            this.loading = false;
          });
        } else {
          this.loading = false;
          return false;
        }
      });
    }
  }
};
</script>

<style>
  #login {
    width: 100%;
    height: 100%;
    background-image: url(../assets/logo.png);
    background-size: 100% 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  #login .el-form {
    width: 400px;
    background: rgba(255, 255, 255, 0.6);
    border-radius: 8px;
    box-shadow: 0 2px 12px 0 rgba(0, 0, 0, .6);
    padding: 20px;
  }
  #login h3 {
    color: #3f3f3f;
    text-align: center;
    font-size: 20px;
    padding: 0 0 20px 0;
  }
</style>
